@import '../../config/variables';


.footer {
    max-width: $max-page-width;
    width: 100%;
    margin: 0 auto;
    margin-top: 55px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-size: $small-font-size;
    text-transform: uppercase;
    flex-wrap: wrap;
    @include breakpoint(mobileonly){
        justify-content: center;
    }
    .column {
        @include breakpoint("phone-tablet"){
            margin-bottom: 0;
        }
        .nav {
            .list {
            display: flex;
            flex-wrap: wrap;
            @include breakpoint("phone-tablet"){
                justify-content: center;
            }
                .listItem{
                    margin-right: 10px;
                    color: $dark-blue;
                    cursor: pointer;
                    @include breakpoint("phone-tablet"){
                        margin-bottom: 15px;
                    }
                }
            }
        }
        .copyright {
            color: $dark-grey;
        }
    }
}