@import '../../config/variables';


.navigation {
    width: 100%;
    @include boxBorder(no);
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
    @include breakpoint("phone") {
        margin-bottom: 0
    }
    .inner {
        max-width: $max-page-width;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 77px;
        padding: 10px 0;
        .column {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 33%;
            &:first-child {
                justify-content: flex-start;
            }
            &:last-child {
                justify-content: flex-end;
            }
            .logo {
                max-width: 250px;
            }
            .searchInput{
                width: 215px;
                box-sizing: border-box;
                padding: 5px;
                border: solid 1px #dbdbdb;
                border-radius: 3px;
                background-color: #fafafa;
                font-size: $normal-font-size;
                font-weight: 300;
                &::placeholder {
                    color: $dark-grey;
                }
                &:focus {
                    outline: none;
                    background-color: white;
                }
            }
            .navIcon {
                margin-left: 30px;
                cursor: pointer;
            }
        }
    }
}