@import '../../config/variables';


.feedPhoto{
    @include  boxBorder(yes);
    width: 100%;
    max-width: $max-card-width;
    margin-bottom: 60px;
    @include breakpoint("phone") {
        background-color: transparent;
        border: 0;
        margin-bottom: 30px;
    }
    &:last-child {
        margin-bottom: 0;
    }
    .header{
        padding: 15px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid $border-color;
        .image {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            margin-right: 15px;
        }
        .headerColumn {
            display: flex;
            flex-direction: column;
            .creator {
                color: orchid;
                font-weight: 600;
                margin-bottom: 5px;
            }
            .location {
                font-size: $small-font-size;
            }
        }
    }
    .photoContainer {
    position: relative;
    padding-bottom: 100%;
    overflow: hidden;
    .mainPhoto {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        }
    }
    .meta {
        padding: 15px;
        padding-bottom: 0;
        padding-top: 10px;
    }
}
