@import '../../config/variables';

.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 16px;
    width: 100%;
    box-sizing: border-box;
    .column {
      display: flex;
      align-items: center;
      .user {
        margin-left: 10px;
        .username {
          font-weight: 600;
          display: block;
          margin-bottom: 3px;
          margin-right: 10px;
        }
        .name{
          color:$dark-grey;
        }
      }
      .bigAvatar {
        width: 55px;
        height: 55px;
        border-radius: 50%;
      }
      .avatar {
        width: 30px;
        height: 30px;
        border-radius: 50%;
      }
      .button {
        @include blueButton();
      }
    }
  }
    
  