@import '../../config/variables';

.auth{
    width: 100%;
    max-width: $max-page-width;
    margin: 0 auto;
    display: flex;
    min-height: 100%;
    margin-top: 30px;
    justify-content: center;
    align-items: center;
    @include breakpoint("phone") {
        margin-top: 10px;
    }
    .column {
        width: 40%;
        @include breakpoint("phone") {
            &:first-child {
                display: none;
            }
        }
        @include breakpoint("tablet") {
            &:first-child {
                display: none;
            }
        }
        &:last-child {
            width: 35%;
            margin-left: 35px;
            @include breakpoint("phone") {
                width: 100%;
                margin: 0;
            }
            @include breakpoint("tablet") {
                width: 70%;
                max-width: 350px;
                marign: 0;
            }
        }
        .appBox {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 35px;
            .appstores{
                margin-top: 25px;
                img {
                    height: 40px;
                    &:first-child {
                        margin-right: 10px;
                    }
                }
            }
            .todo{
                color: grey;
            }
            
        }
        .whiteBox{
            text-align: center;
            padding: 15px;
            @include boxBorder(no);
            @include breakpoint("phone") {
                background-color: transparent;
                border: 0;
            }
            .changeLink{
                color: $light-blue;
                cursor: pointer;
            }
            p{
                margin:15px;
            }
        }
        .formBox{
            margin-bottom: 10px;
            padding: 0 40px;
            padding-top: 30px;
            img{
                height: 60px;
            }
        }
    }
}